#ecSch {
  width: 100%;
  padding-bottom: 2rem;
  color: #3f3f3f;
}
#exSch.dark {
  color: #b4b4b4;
}

#exSchView {
  display: flex;
  flex-direction: column;
  height: auto;
  width: 100%;
  max-width: 614px;
  position: relative;
  padding: 2rem 2px 0.5rem 2px;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
#exSchView > h1 {
  font-size: 2rem;
  font-weight: 900;
  margin: 0 0 0 2rem;
}

#exTable {
  display: flex;
  position: relative;
  overflow: auto;
  width: 100%;
  white-space: nowrap;
  padding-bottom: 3rem;
  padding-left: 0.75rem;
  padding-right: calc(0.75rem - 0.5rem);
  left: 0;
  max-width: 720px;
}
.exTableRows {
  display: flex;
  flex-direction: column;
  margin-right: 0.5rem;
  width: 28%;
  min-width: 94px;
}
.exTableRows.top {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-right: 0.5rem;
  width: 15%;
  min-width: 48px;
  margin-right: 0;
}
.exTableTopTitle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  font-weight: 700;
  padding: 0.5rem;
  color: #5f5f5f;
}
.exTableLeftTitle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
  /* margin-left: 0.75rem; */
  height: 2.4rem;
  max-width: 35px;
  padding: 0.75rem;
  font-size: 0.75rem;
  color: #5f5f5f;
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
.exTableData {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 2.4rem;
  font-size: 10px;
  color: #ffffff;
  padding: 0.5rem 0.7rem 0.5rem 0.7rem;
  margin-bottom: 5px;
  border-radius: 1.2rem;
  background: linear-gradient(165deg, #7ec17e, #6894af);
  -webkit-border-radius: 1.2rem;
  -moz-border-radius: 1.2rem;
  -ms-border-radius: 1.2rem;
  -o-border-radius: 1.2rem;
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
.dark .exTableData {
  color: #d1d1d1;
  background: linear-gradient(165deg, #4e814e, #3e5c6f);
}

@media (min-width: 536px) {
  .exTableData {
    font-size: calc(10px + 0.25vw);
  }
}

/* 多行多列塊規則 */
.exTableData.Empty {
  opacity: 0;
}
.exTableData.V2 {
  height: calc(4.8rem + 5px);
}
.exTableData.Double {
  width: calc(200% + 0.5rem);
}
.exTableData.V3 {
  height: calc(7.2rem + 10px);
}
.exTableData.Triple {
  width: calc(300% + 1rem);
}
.exTableData.notEx {
  color: #5f5f5f;
  background: #dadada;
}
.dark .exTableData.notEx {
  color: #d1d1d1;
  background: #252525;
}

@media (min-height: 783px) {
  #exSch {
    scroll-snap-type: y mandatory;
    scroll-padding-top: calc(2rem + 54px);
  }
  #exSchView h1 {
    scroll-snap-align: start;
  }
}
