.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 54px 0 0 0;
}
.board {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.game-board {
  padding-top: 2rem;
}
.square {
  background: #fff;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 50px;
  margin-right: 6px;
  margin-top: 6px;
  padding: 0;
  text-align: center;
  width: 50px;
  border-radius: 12px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
  font-size: 2rem;
  font-weight: 700;
}

.game-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.game-info div {
  padding: 6px 9px;
  margin: 3px 0;
  background: #e3e3e3;
  border-radius: 9px;
  -webkit-border-radius: 9px;
  -moz-border-radius: 9px;
  -ms-border-radius: 9px;
  -o-border-radius: 9px;
}
