/* === Universal Setting ========================================== */
* {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  display: flex;
  font-family: 'Inter', 'Prompt', 'Noto Sans TC';
  font-size: 16px;
  color: #333;
  background: #0f0f0f;
  height: 100%;
  width: 100%;
  overflow: hidden;
  /* scroll-behavior: smooth; */
}
ul,
ol {
  list-style: none;
}
:focus {
  outline: none;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  user-select: none;
}

#root {
  width: 100%;
}

.target-fix {
  position: relative;
  top: -30px;
  display: block;
  height: 0;
  overflow: hidden;
}

#Tips {
  z-index: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: calc(100% - 24px);
  max-width: 480px;
  min-width: calc(264px - 24px);
  bottom: calc(68px + 12px);
  padding: 0.7rem 0.7rem 0.7rem 0.9rem;
  background: #cdcdcdb1;
  margin-left: 12px;
  box-shadow: 0 3px 20px 0 #ffffff;
  border-radius: 0.8rem;
  -webkit-border-radius: 0.8rem;
  -moz-border-radius: 0.8rem;
  -ms-border-radius: 0.8rem;
  -o-border-radius: 0.8rem;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.dark #Tips {
  background: #3636369a;
  box-shadow: 0 3px 20px 0 #000;
}
/* 提示區塊 */
#Tips div span {
  color: #3c3c3c;
  font-weight: 500;
  font-size: 1rem;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.dark #Tips div span {
  color: #e1e1e1;
}
#closeBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 23px;
  width: 23px;
  background: #c94a4ad9;
  border-radius: 0.3rem;
  -webkit-border-radius: 0.3rem;
  -moz-border-radius: 0.3rem;
  -ms-border-radius: 0.3rem;
  -o-border-radius: 0.3rem;
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
.BtnLineL {
  position: absolute;
  height: 1px;
  width: 12px;
  border-radius: 0.5px;
  background: #e4bdbd;
  -webkit-border-radius: 0.5px;
  -moz-border-radius: 0.5px;
  -ms-border-radius: 0.5px;
  -o-border-radius: 0.5px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
.BtnLineR {
  position: absolute;
  height: 1px;
  width: 12px;
  border-radius: 0.5px;
  background: #e4bdbd;
  -webkit-border-radius: 0.5px;
  -moz-border-radius: 0.5px;
  -ms-border-radius: 0.5px;
  -o-border-radius: 0.5px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
#closeBtn:hover {
  width: 25px;
  height: 25px;
  margin-right: -1px;
  background: #8a4747af;
  border-radius: calc(0.3rem + 2px);
  -webkit-border-radius: calc(0.3rem + 1px);
  -moz-border-radius: calc(0.3rem + 2px);
  -ms-border-radius: calc(0.3rem + 2px);
  -o-border-radius: calc(0.3rem + 2px);
}
#closeBtn:hover > .BtnLineL {
  width: 14px;
  background: #c99999;
}
#closeBtn:hover > .BtnLineR {
  width: 14px;
  background: #c99999;
}

