/* === Universal Style Setting ========================================== */

main {
  position: fixed;
  width: 100%;
  min-width: 264px;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 48px;
  overflow-y: scroll;
  background: #f1f1f1;
  padding: 54px 0 68px 0;
  transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
}
main.dark {
  background: #131313;
}

@media (min-width: 823px) {
  main {
    width: calc(100% - 72px);
    right: 0;
  }
}

.display-none {
  display: none;
}
.opacity-0 {
  opacity: 0;
}
.navSpace {
  height: 48px;
}
#refreshBtn {
  position: fixed;
  z-index: 9000;
  right: 12px;
  bottom: 72px;
  height: 48px;
  width: 48px;
  background: #df5050;
  color: #fff;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
}

.loader {
  width: 48px;
  height: 48px;
  margin: auto;
  left: 50%;
  top: 50%;
  position: fixed;
  transform: translate(-50%, -100%);
  -webkit-transform: translate(-50%, -100%);
  -moz-transform: translate(-50%, -100%);
  -ms-transform: translate(-50%, -100%);
  -o-transform: translate(-50%, -100%);
}
.loader:before {
  content: '';
  width: 48px;
  height: 5px;
  background: #6c6c6c;
  opacity: 0.25;
  position: absolute;
  top: 60px;
  left: 0;
  border-radius: 50%;
  animation: shadow 0.5s linear infinite;
}
.loader:after {
  content: '';
  width: 50px;
  height: 50px;
  background: #808080;
  animation: bxSpin 1s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 13px;
  -webkit-border-radius: 13px;
  -moz-border-radius: 13px;
  -ms-border-radius: 13px;
  -o-border-radius: 13px;
  -webkit-animation: bxSpin 0.5s linear infinite;
}
@keyframes bxSpin {
  17% {
    border-bottom-left-radius: 13px;
  }
  25% {
    transform: translateY(9px) rotate(-22.5deg);
    -webkit-transform: translateY(9px) rotate(-22.5deg);
    -moz-transform: translateY(9px) rotate(-22.5deg);
    -ms-transform: translateY(9px) rotate(-22.5deg);
    -o-transform: translateY(9px) rotate(-22.5deg);
  }
  50% {
    transform: translateY(18px) scale(1, 0.9) rotate(-45deg);
    border-bottom-left-radius: 37px;
    -webkit-transform: translateY(18px) scale(1, 0.9) rotate(-45deg);
    -moz-transform: translateY(18px) scale(1, 0.9) rotate(-45deg);
    -ms-transform: translateY(18px) scale(1, 0.9) rotate(-45deg);
    -o-transform: translateY(18px) scale(1, 0.9) rotate(-45deg);
  }
  75% {
    transform: translateY(9px) rotate(-67.5deg);
    -webkit-transform: translateY(9px) rotate(-67.5deg);
    -moz-transform: translateY(9px) rotate(-67.5deg);
    -ms-transform: translateY(9px) rotate(-67.5deg);
    -o-transform: translateY(9px) rotate(-67.5deg);
  }
  100% {
    transform: translateY(0) rotate(-90deg);
    -webkit-transform: translateY(0) rotate(-90deg);
    -moz-transform: translateY(0) rotate(-90deg);
    -ms-transform: translateY(0) rotate(-90deg);
    -o-transform: translateY(0) rotate(-90deg);
  }
}

@keyframes shadow {
  0%,
  100% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.2, 1);
  }
}

