@media (min-width: 991px) {
  #Setting {
    width: calc(100% - 124px);
  }
  #Setting.open {
    left: 124px;
  }
  #settingBottomMask {
    left: 124px;
    width: calc(100% - 124px);
  }
}

#chat {
  display: flex;
  position: fixed;
  flex-direction: column;
  z-index: 5500;
  width: 48px;
  height: 48px;
  top: 72px;
  right: 24px;
  margin: 0;
  background: linear-gradient(150deg, #7e3ec2, #427bb8);
  padding: 0;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
  transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
}
#chat.open {
  display: flex;
  position: fixed;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  top: 0;
  right: 0;
  background: linear-gradient(150deg, #7e3ec2, #427bb8);
  padding: 0 18px 18px 18px;
  border-radius: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  -o-border-radius: 0;
  -webkit-border-radius: 0;
}
.closeOpenChat {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 5510;
  height: 48px;
  width: 48px;
  padding: 0 24px;
  bottom: calc(100vh - 120px);
  right: 24px;
  background: #7e3ec2;
  color: #fff;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
  transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.6s cubic-bezier(0.5, 0, 0.3, 1);
}
.closeOpenChat.open {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  height: 48px;
  padding: 0 24px;
  bottom: 18px;
  right: calc(100% - 66px);
  background: #da3939;
  color: #fff;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
  transform: rotate(720deg);
  -webkit-transform: rotate(720deg);
  -moz-transform: rotate(720deg);
  -ms-transform: rotate(720deg);
  -o-transform: rotate(720deg);
}
#chatContent {
  display: flex;
  flex-direction: column;
  height: 24px;
  width: 100%;
  opacity: 0;
  margin-bottom: 0;
  padding: 48px 0;
  color: #fff;
  border-radius: 24px 24px 0 0;
  -webkit-border-radius: 24px 24px 0 0;
  -moz-border-radius: 24px 24px 0 0;
  -ms-border-radius: 24px 24px 0 0;
  -o-border-radius: 24px 24px 0 0;
  transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
}
#chat.open #chatContent {
  height: 100%;
  width: 100%;
  opacity: 1;
  margin-bottom: 6px;
  border-radius: 12px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
  overflow: auto;
}
#chatForm {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.chatInput {
  height: 24px;
  width: 24px;
  padding: 0;
  font-size: 0.7rem;
  background: #ffffffaf;
  opacity: 0;
  margin-right: 6px;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
  transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
}
#chat.open .chatInput {
  position: relative;
  height: 48px;
  width: calc(100% - 108px);
  padding: 12px 18px 12px 18px;
  font-size: 1rem;
  background: #ffffffaf;
  opacity: 1;
  bottom: 0;
  margin-right: 6px;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
}
.chatSubmit {
  width: 0;
  height: 0;
  opacity: 0;
  background: linear-gradient(150deg, #8648b9, #a14169);
  color: #fff;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
  transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
}
#chat.open .chatSubmit {
  width: 48px;
  height: 48px;
  opacity: 1;
  background: linear-gradient(150deg, #8648b9, #a14169);
  color: #fff;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
}
#chatContent .user {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: max-content;
  padding: 6px 9px;
  background: #ffffffef;
  color: #3f3f3f;
  margin: 12px 0;
  border-radius: 12px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
}

#openRepProb.open {
  height: 54px;
  padding: 0 24px;
  bottom: 24px;
  opacity: 1;
  font-size: 1rem;
}
#openRepProb {
  z-index: 6001;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: 24px;
  padding: 0 12px;
  bottom: 0;
  left: 24px;
  opacity: 0;
  font-size: 0.85rem;
  background: #f1f1f1;
  color: #2f2f2f;
  border-radius: 27px;
  -webkit-border-radius: 27px;
  -moz-border-radius: 27px;
  -ms-border-radius: 27px;
  -o-border-radius: 27px;
  transition: 0.95s;
  -webkit-transition: 0.95s;
  -moz-transition: 0.95s;
  -ms-transition: 0.95s;
  -o-transition: 0.95s;
}
.dark #openRepProb {
  color: #afafaf;
  background: #2c2c2c;
}
